import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {AuthService} from '../../app/auth.service';
import { Storage } from '@ionic/storage';
import { LoadingController,MenuController,ToastController } from '@ionic/angular';
import {
  FormControl,
  FormGroup,
  Validators,
  ValidatorFn,
  AbstractControl
} from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage  {

  // loginForm: any;
  username:any;
  password:any;
  res : any;
  dealers:any;
  dealername:any;
  loading:any;
  myForm: any;
  toast: any;
  isremember:boolean = false;
  islogin:boolean = false;
  ISEnable: any;
  showpass:boolean = false;
  type:any='password';
  constructor( public toastController: ToastController,private router: Router,public loadingController: LoadingController,private authservice:AuthService,public storage : Storage, public menuCtrl: MenuController) { 
    this.menuCtrl.enable(false);
  
  }

  ngOnInit() {
 
    this.myForm = new FormGroup({
      // name: new FormControl('', [Validators.required, Validators.minLength(1)]),
      username: new FormControl('', [Validators.required]),
      password: new FormControl('', Validators.compose([
        // Validators.minLength(6),
        Validators.required,
        // Validators.pattern('^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])[a-zA-Z0-9]+$') //this is for the letters (both uppercase and lowercase) and numbers validation
      ])),
      // mob: new FormControl('', [Validators.required, Validators.minLength(13),Validators.maxLength(13)])
    });

  }

  ionViewWillEnter(){
    this.storage.get('email').then((val) => {
      console.log(val);
      if(val != ''){
      this.username = val;
      }
      else{
        this.username = "";
      }
    });
    this.storage.get('password').then((val) => {
      console.log(val);
      if(val != ''){
      this.password = val;
      }
      else{ 
        this.password = "";
      }
      if (this.password != '') {
        this.isremember = true;
      }
      else {
        this.isremember = false;
      }

    });
  }

  showpassword(){
   this.showpass = !this.showpass;
   if(this.showpass){
     this.type = 'text';
   }
   else{
     this.type='password';
   }
    
  }


  Login() {
    if(this.username == '' || this.username == undefined || this.username == null){
      this.authservice.showToast("Please Enter Username");
    }else if(this.password == '' || this.password == undefined || this.password == null){
      this.authservice.showToast("Please Enter Password");
    }else {
      this.authservice.presentLoading();
      this.authservice.doLogin(this.username,this.password).subscribe(res =>{
        this.res = res;
      if(this.res.Code == 200){
        this.storage.set("userid",this.res.pkemployeeid);
        this.storage.set("dealerid",this.res.DealershipId);
        this.dealers = this.authservice.getdealers();
        for(let i=0 ; i<this.dealers.length;i++){
          if(this.dealers[i].DealershipId == this.res.DealershipId){
            this.dealername = this.dealers[i].DealershipName;
            console.log(this.dealername);
            this.storage.set("dealername",this.dealername);
          }
       }
       if(this.res.DesignationName == "General Manager"){
        this.ISEnable = false;
       }else{
        this.ISEnable = true;
       }
       let object = {
        ISEnable: this.ISEnable
        }
        this.authservice.dismissLoading();
        this.storage.set('islogin',true);
        this.router.navigate(['/home'],{ queryParams: object });
      }else if(this.res.Code == 400){
        this.authservice.dismissLoading();
        this.authservice.showToast(this.res.msg);
      }
      })
    }
  }

  isLogin() {
    if (this.myForm.valid) {
      return true;
    }
    else {
      return false;
    }
  }

  rememberme(event) {
    console.log(event);
    if (event.detail.checked == true) {
      console.log('enter in');
      this.storage.set('email', this.username);
      this.storage.set('password', this.password);
    }
    else {
      this.storage.set('email','');
      this.storage.set('password','');
    }
  }


}
